<template>
  <v-content>
    <v-layout>
      <app-header></app-header>
      <v-container>
        <v-dialog v-model="productDialog" fullscreen transition="dialog-bottom-transition" :overlay="false" scrollable>
          <v-card>
            <v-toolbar card dark color="primary">
              <v-btn icon @click.native="productDialog = false" dark>
                <v-icon>close</v-icon>
              </v-btn>
              <v-toolbar-title>Product</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-toolbar-items>
                <v-btn dark flat v-if="!product._id" @click.native="save()">Save</v-btn>
                <v-btn dark flat v-if="product._id" @click.native="update()">Update</v-btn>
              </v-toolbar-items>
            </v-toolbar>

            <v-card>
              <v-card-title>
                <span class="headline">User Profile</span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap>
                    <v-flex xs12 sm6 md4>
                      <v-text-field label="Legal first name*" required></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6 md4>
                      <v-text-field label="Legal middle name" hint="example of helper text only on focus"></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6 md4>
                      <v-text-field label="Legal last name*" hint="example of persistent helper text" persistent-hint
                        required></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field label="Email*" required></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field label="Password*" type="password" required></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6>
                      <v-select :items="['0-17', '18-29', '30-54', '54+']" label="Age*" required></v-select>
                    </v-flex>
                    <v-flex xs12 sm6>
                      <v-autocomplete :items="['Skiing', 'Ice hockey', 'Soccer', 'Basketball', 'Hockey', 'Reading', 'Writing', 'Coding', 'Basejump']"
                        label="Interests" multiple></v-autocomplete>
                    </v-flex>
                  </v-layout>
                </v-container>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" flat @click.native="dialog = false">Close</v-btn>
                <v-btn color="blue darken-1" flat @click.native="dialog = false">Save</v-btn>
              </v-card-actions>
            </v-card>

            <v-list three-line subheader>
              <v-subheader>Product</v-subheader>
              <v-divider></v-divider>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-text-field label="Product Name " v-model="product.name"></v-text-field>
                </v-list-tile-content>
              </v-list-tile>

              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-text-field label="Product Description" v-model="product.description"></v-text-field>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-text-field label="Product category" v-model="product.category_id"></v-text-field>
                </v-list-tile-content>
              </v-list-tile>
              <v-btn color="blue darken-1" flat @click.native="dialog = true">Add Category</v-btn>

              {{categoryId}}
              <v-select v-model="categoryId" :items="categoryList" max-height="100" autocomplete chips label="Chips"
                item-text="name" item-value="code">
                <template slot="selection" slot-scope="{ item, index }">
                  <v-chip>
                    <span>{{ item.name }}</span>
                  </v-chip>
                </template>

                <template slot="item" slot-scope="data">
                  <template v-if="typeof data.item !== 'object'">
                    <v-list-tile-content v-text="data.item"></v-list-tile-content>
                  </template>
                  <template v-else>
                    <v-list-tile-avatar class="dropdown_avatar">
                      <span class="red--text"> {{ data.item.name.charAt(0).toUpperCase() }}</span>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                      <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                      <v-list-tile-sub-title v-html="data.item.code"></v-list-tile-sub-title>
                    </v-list-tile-content>
                  </template>
                </template>



              </v-select>


              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-text-field label="Product Code " v-model="product.code"></v-text-field>
                </v-list-tile-content>
              </v-list-tile>

              <v-checkbox :label="`Has Dimentions`" v-model="product.hasSize"></v-checkbox>

              <v-list-tile avatar v-if="product.hasSize">
                <v-list-tile-content>
                  <v-text-field label="Height" v-model="product.size.height" required></v-text-field>
                  <v-text-field label="Width" v-model="product.size.width" required></v-text-field>
                </v-list-tile-content>
              </v-list-tile>


              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-text-field label="Product Price" v-model="product.price" required></v-text-field>
                </v-list-tile-content>
              </v-list-tile>

              <!---  dialog -->


            </v-list>
            <v-divider></v-divider>
          </v-card>
        </v-dialog>

        <v-dialog v-model="dialog" persistent max-width="300px">
          <v-card>
            <v-card-title>
              <span class="headline">Category</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 md12>
                    <v-text-field label="Category Name*" v-model="category.name" required></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm12 md12>
                    <v-text-field label="Category Code" v-model="category.code"></v-text-field>
                  </v-flex>

                </v-layout>
              </v-container>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click.native="dialog = false">Close</v-btn>
              <v-btn color="blue darken-1" flat @click.native="addCategory()">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-btn color="indigo" fab dark absolute top left @click="create()">
          <v-icon dark>group_add</v-icon>
        </v-btn>


        <v-expansion-panel>
          <v-expansion-panel-content v-for="(item,i) in 1" :key="i">
            <div slot="header">Product</div>
            <v-card>
              <v-card-title>
                <span class="headline">User Profile</span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap>
                    <v-flex xs12 sm6 md4>
                      <v-text-field label="Legal first name*" required></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6 md4>
                      <v-text-field label="Legal middle name" hint="example of helper text only on focus"></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6 md4>
                      <v-text-field label="Legal last name*" hint="example of persistent helper text" persistent-hint
                        required></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field label="Email*" required></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field label="Password*" type="password" required></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6>
                      <v-select :items="['0-17', '18-29', '30-54', '54+']" label="Age*" required></v-select>
                    </v-flex>
                    <v-flex xs12 sm6>
                      <v-autocomplete :items="['Skiing', 'Ice hockey', 'Soccer', 'Basketball', 'Hockey', 'Reading', 'Writing', 'Coding', 'Basejump']"
                        label="Interests" multiple></v-autocomplete>
                    </v-flex>
                  </v-layout>
                </v-container>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" flat @click.native="dialog = false">Close</v-btn>
                <v-btn color="blue darken-1" flat @click.native="dialog = false">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-data-table :headers="headers" :items="productList" item-key="name" :search="search" class="elevation-1">
          <template slot="items" slot-scope="props">
            <td>{{ props.item.code }}</td>
            <td>{{ props.item.name }}</td>
            <td>{{ props.item.description }}</td>
            <td>{{ props.item.price }}</td>

          </template>
        </v-data-table>
      </v-container>
      <app-footer></app-footer>
    </v-layout>
  </v-content>
</template>
<script>
  import Axios from 'axios'
  import Authentication from '@/components/pages/Authentication'
  import APIurlConfig from '../../apiConfig'
  const apiURL = APIurlConfig.API_URL // 'http://localhost:3001'
  export default {
    data() {
      return {
        validated: 1,
        loginPage: false,
        productDialog: false,
        dialog: false,
        productList: [],
        categoryList: [],
        categoryId: '',
        product: {
          size: {
            height: 0,
            width: 0,
          }
        },
        category: {
          name: '',
          code: ''
        },
        headers: [{
            text: 'Code',
            align: 'center',
            sortable: false,
            value: 'code'
          },
          {
            text: 'Name',
            align: 'center',
            value: 'Action'
          },
          {
            text: 'Desc',
            align: 'center',
            value: 'Role'
          },
          {
            text: 'Price',
            align: 'center',
            value: 'Role'
          }
        ]
      }
    },
    mounted() {
      this.getAllProduct(),
        this.getCategory()
    },
    methods: {
      getAllProduct(context) {
        Axios.get(`${apiURL}/api/v1/product`, {
          headers: {
            'Authorization': Authentication.getAuthenticationHeader(this)
          }
        }).then(({
          data
        }) => (
          this.productList = data,
          console.log(data)
        ))
      },

      create() {
        this.productDialog = true;
      },

      save() {
        Axios.post(`${apiURL}/api/v1/product`, this.product)
          .then(({
            data: {
              token
            }
          }) => {
            //this.getAllUsers();
            console.log(data);
          }).catch(({
            response: {
              data
            }
          }) => {})
      },

      getCategory() {
        Axios.get(`${apiURL}/api/v1/category`, {
          headers: {
            'Authorization': Authentication.getAuthenticationHeader(this)
          }
        }).then(({
          data
        }) => (
          this.categoryList = data,
          console.log(data)
        ))

      },

      addCategory() {
        Axios.post(`${apiURL}/api/v1/category`, this.category)
          .then(({
            data: {
              token
            }
          }) => {
            //this.getAllUsers();
            this.getCategory();
            console.log(data);
          }).catch(({
            response: {
              data
            }
          }) => {})
      },

    }
  }

</script>



